<template>
  <div class="link">
    <div class="container">
      <div class="columns is-multiline">
        <div class="column is-12">
          <Aads1></Aads1>
        </div>
        <div class="column is-3">
          <Juicy2></Juicy2>
        </div>
        <div class="column is-6">
          <div class="blockito">
            <div class="card">
              <div class="card-header is-shadowless">
                <h1 class="card-header-title has-text-primary is-size-4">
                  {{ link.name }}
                </h1>
              </div>
              <div class="card-content py-3">
                <Aads2></Aads2>
                <div class="mt-6 mb-5">
                  <center>
                    <vue-hcaptcha
                      sitekey="93d9339c-ca9d-4ad3-b384-292836c0289f"
                      @verify="onCallback"
                      @expired="onExpired"
                      @error="onError"></vue-hcaptcha>
                  </center>
                </div>
              </div>
              <template v-if="verified">
                <Aads2></Aads2>
                <label class="card-footer" for="generate">
                  <a v-if="!gift" class="card-footer-item" @click.prevent="generate()">Generate</a>
                </label>
              </template>
            </div>

            <template v-if="gift">
              <Aads2></Aads2>
              <div class="card my-6">
                <div class="card-content">
                  <center>
                    <b-button
                      type="is-success"
                      tag="router-link"
                      :to="{ name: 'WebGenerate', params: {id: gift.id , hash: gift.hash}}"
                    >Ir</b-button>
                  </center>
                </div>
              </div>
            </template>
          </div>
        </div>
        <div class="column is-3">
          <Juicy1></Juicy1>
        </div>
        <div class="column is-12">
          <Aads1></Aads1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Link from '../../api/Link'
import VueHcaptcha from '@hcaptcha/vue-hcaptcha'


import Aads2 from '@/components/ads/A-ADS2.vue'
import Aads1 from '@/components/ads/A-ADS1.vue'
import Juicy1 from '@/components/ads/Juicy1.vue'
import Juicy2 from '@/components/ads/Juicy2.vue'


export default {
  name: 'WebLink',
  components: {
    VueHcaptcha, Aads1, Aads2, Juicy1, Juicy2
  },
  data() {
    return {
      link: {},
      formLink: {
        identy: '',
        link_id: ''
      },
      gift: null,
      verified: false
    }
  },
  methods: {
    generate: function () {
      Link.generate(this.formLink).then((response) => {
        if(response.status === 201) {
          this.$buefy.notification.open({
              message: 'Create successfull!',
              type: 'is-success'
          })
          this.gift = response.data
        }
      }).catch((errors) => {
        if(errors.response.status === 422){
          this.$buefy.notification.open({
            type: 'is-info',
            message: 'Error'
          })
        }
      })
    },
    onCallback: function (response) {
      this.verified = true
      console.log(response)
    },

    onExpired: function() {
      this.verified = false
    },

    onError: function() {
      this.$buefy.notification.open({
        message: 'Error auth Captcha',
        type: 'is-danger'
      })
    }
  },
  mounted() {
    Link.getWebLink(this.$route.params.id).then((response) => {
      if(response.status === 200) {
        this.link = response.data
        this.formLink.identy = response.data.identy
        this.formLink.link_id = response.data.id
      }
    }).catch((errors) => {
      if(errors.response.status === 404){
        this.$router.push({name: '404'})
      }
    })
  }
}
</script>
